* {
    margin: 0;
    padding: 0;
}
body{
    background-image:linear-gradient(to bottom,rgba(12, 23, 83),rgba(12, 23, 83),white,white,white,white )
}
.abc{
    color:white;
}


a {
    text-decoration: none;
    transition: 0.3s;
    font-size: 22px;
    color: rgb(255, 255, 255);
    padding-right: 20%;
        
}


nav {
    color:rgb(255, 255, 255) ;
    display: flex;
    flex-direction:row;
    align-items: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    height: 8vh;
    
}
#logo{
    color:white;
    
}

/* .nav-list {
    list-style: none;
    display: flex;
    background-color: rgb(255, 243, 71);
} */

.nav-list li {
    letter-spacing: 3px;
}
.sub-titulo {
    text-align: initial;
    padding-left: 4%;
    list-style-type: none;
    color:rgb(255, 255, 255);
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.img-container {
    width: auto;
    height: 30px;
    padding: 0px 10px 0px 10px;    
 
}

 .caixa{
     position: relative;
     align-items: center;
     height: 40px;
     width: 400px;
     margin-top: 3%;
 }

.navigation {
    position: absolute;
    bottom: 20px;
    transform: translate(-50%);

}
.home .card{
    border: 1px solid gray;
    border-radius: 4px;
    background-color: rgb(255, 255, 255);
    margin: 40px;

    height:20px;
    width: 50px;
}
.home{
    display: flex;
    /* border: 2px orange solid; */
}

.card .nome{
    text-align: center;
    font-size: 19px;
    display: flex;
    margin-top: -8%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}


/* Quebra de pagina */
@media screen and (max-width: 850px){ 
    .caixa{
        display: none;
    }
}

 

